import global from '~/mixin/global'
export default {
  name: "SocialLink",
  props: ['social'],
  mixins: [global],
  methods: {
    setThemeColor(theme){
      switch (theme) {
        case "red":
          return 'hover:bg-dribble'
        case "blue": 
          return 'hover:bg-facebook'
        default:
          return 'hover:bg-black'
      }
    }
  }
};
